import { State, TransformsPlugin, RenderPlugin, TweenPlugin } from 'shallot';
import { OrbitPlugin } from 'shallot/extras';
import { setupSequenceRecording } from '@blog-components/recorder';
import { createStepPlugin, type SequenceMap } from '@blog-components/sequence';

const STEP_SEQUENCES: SequenceMap = {
  '0-1': 'step-0-1',
  '1-0': 'step-1-0',
};

function setupCanvas(canvas: HTMLCanvasElement): void {
  const rect = canvas.getBoundingClientRect();
  canvas.width = rect.width * window.devicePixelRatio;
  canvas.height = rect.height * window.devicePixelRatio;
}

async function init(): Promise<void> {
  const canvas = document.getElementById('canvas-1') as HTMLCanvasElement;
  if (!canvas) return;

  setupCanvas(canvas);
  window.addEventListener('resize', () => setupCanvas(canvas));

  const state = await State.new()
    .withCanvas(canvas)
    .withPlugins(
      TransformsPlugin,
      RenderPlugin,
      OrbitPlugin,
      TweenPlugin,
      createStepPlugin(STEP_SEQUENCES)
    )
    .withScene('/scenes/steps.scene')
    .build();

  canvas.__state__ = state;

  let lastTime = performance.now();
  const tick = (now: number) => {
    const dt = (now - lastTime) / 1000;
    lastTime = now;
    state.step(dt);
    requestAnimationFrame(tick);
  };
  requestAnimationFrame(tick);

  setupSequenceRecording({
    canvasSelector: '#canvas-1',
    sequenceMap: STEP_SEQUENCES,
  });
}

if (typeof document !== 'undefined') {
  if (document.readyState === 'loading') {
    document.addEventListener('DOMContentLoaded', init);
  } else {
    init();
  }
}
