import { Pair } from 'bitecs';
import { Muxer, ArrayBufferTarget } from 'mp4-muxer';
import type { State } from 'shallot';
import { Sequence, SequenceState, Tween, TweenState, ChildOf } from 'shallot';
import type { RecordingConfig } from './types';
import { DEFAULT_CONFIG } from './types';
import { downloadBlob } from './encoder';
import { StepController, getEntityByName, type SequenceMap } from '@blog-components/sequence';

export interface SequenceRecordingConfig {
  canvasSelector: string;
  sequenceMap: SequenceMap;
  recordingConfig?: Partial<RecordingConfig>;
}

let isRecording = false;

function getMaxStepFromMap(sequenceMap: SequenceMap): number {
  let max = 0;
  for (const key of Object.keys(sequenceMap)) {
    const [from, to] = key.split('-').map(Number);
    max = Math.max(max, from, to);
  }
  return max;
}

async function encodeSequenceFrames(
  canvas: HTMLCanvasElement,
  state: State,
  seqEid: number,
  config: RecordingConfig,
  onProgress?: (frame: number, total: number) => void
): Promise<Blob> {
  Sequence.state[seqEid] = SequenceState.IDLE;
  Sequence.elapsed[seqEid] = 0;
  for (const childEid of state.query([Pair(ChildOf.relation, seqEid), Tween])) {
    Tween.state[childEid] = TweenState.IDLE;
    Tween.elapsed[childEid] = 0;
  }
  Sequence.state[seqEid] = SequenceState.PLAYING;

  let maxDuration = 0;
  for (const childEid of state.query([Pair(ChildOf.relation, seqEid), Tween])) {
    maxDuration = Math.max(maxDuration, Tween.duration[childEid]);
  }
  const duration = maxDuration + 0.1;
  const totalFrames = Math.ceil(duration * config.fps);

  const muxer = new Muxer({
    target: new ArrayBufferTarget(),
    video: { codec: 'avc', width: config.width, height: config.height },
    fastStart: 'in-memory',
  });

  const encoder = new VideoEncoder({
    output: (chunk, meta) => muxer.addVideoChunk(chunk, meta),
    error: (e) => console.error('Encoder error:', e),
  });

  encoder.configure({
    codec: 'avc1.640032',
    width: config.width,
    height: config.height,
    bitrate: config.bitrate,
    framerate: config.fps,
  });

  const stepTime = 1 / config.fps;

  for (let i = 0; i < totalFrames; i++) {
    state.step(stepTime);

    const videoFrame = new VideoFrame(canvas, {
      timestamp: (i * 1_000_000) / config.fps,
    });
    encoder.encode(videoFrame);
    videoFrame.close();

    if (i % 30 === 0) {
      onProgress?.(i, totalFrames);
      await new Promise((r) => setTimeout(r, 0));
    }
  }

  await encoder.flush();
  muxer.finalize();

  return new Blob([muxer.target.buffer], { type: 'video/mp4' });
}

function createUI(
  config: RecordingConfig,
  sequenceMap: SequenceMap,
  onRecord: (fromStep: number, toStep: number) => Promise<void>,
  onStepChange: (fromStep: number, toStep: number) => void,
  updateStatus: (msg: string) => void
): { updateStep: (step: number, max: number) => void } {
  let currentStep = 0;
  let maxStep = 0;

  const ui = document.createElement('div');
  ui.id = 'recording-ui';
  ui.innerHTML = `
    <style>
      #recording-ui {
        position: fixed;
        bottom: 20px;
        right: 20px;
        background: rgba(0,0,0,0.9);
        padding: 15px;
        border-radius: 8px;
        font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
        font-size: 14px;
        color: #fff;
        z-index: 9999;
        min-width: 200px;
      }
      #recording-ui .step-display {
        margin-bottom: 12px;
        font-size: 16px;
        text-align: center;
      }
      #recording-ui .nav-row {
        display: flex;
        gap: 8px;
        margin-bottom: 8px;
      }
      #recording-ui .nav-row button {
        flex: 1;
        padding: 8px;
        border: none;
        border-radius: 4px;
        cursor: pointer;
        background: #444;
        color: #fff;
      }
      #recording-ui .nav-row button:hover { background: #555; }
      #recording-ui .nav-row button:disabled { opacity: 0.5; cursor: not-allowed; }
      #recording-ui button.record {
        width: 100%;
        padding: 10px;
        border: none;
        border-radius: 4px;
        cursor: pointer;
        background: #c33;
        color: #fff;
        font-weight: bold;
      }
      #recording-ui button.record:hover { background: #e55; }
      #recording-ui button.record:disabled { opacity: 0.5; cursor: not-allowed; }
      #recording-ui #rec-status { margin-top: 10px; color: #888; font-size: 12px; }
      body.recording-mode #recording-ui { display: none; }
      body.recording-mode { margin: 0; padding: 0; overflow: hidden; background: #000; }
      body.recording-mode canvas.recording-target {
        position: fixed !important;
        top: 0 !important;
        left: 0 !important;
      }
    </style>
    <div class="step-display">Step <span id="current-step">0</span> / <span id="max-step">0</span></div>
    <div class="nav-row">
      <button id="rec-prev">Prev</button>
      <button id="rec-next">Next</button>
    </div>
    <button id="rec-record" class="record">Record Next (R)</button>
    <div id="rec-status">Ready</div>
  `;

  document.body.appendChild(ui);

  const currentStepEl = document.getElementById('current-step')!;
  const maxStepEl = document.getElementById('max-step')!;
  const prevBtn = document.getElementById('rec-prev') as HTMLButtonElement;
  const nextBtn = document.getElementById('rec-next') as HTMLButtonElement;
  const recordBtn = document.getElementById('rec-record') as HTMLButtonElement;
  const statusEl = document.getElementById('rec-status')!;

  const updateDisplay = () => {
    currentStepEl.textContent = String(currentStep);
    maxStepEl.textContent = String(maxStep);
    prevBtn.disabled = currentStep <= 0 || isRecording;
    nextBtn.disabled = currentStep >= maxStep || isRecording;
    const hasNextSequence = sequenceMap[`${currentStep}-${currentStep + 1}`] !== undefined;
    recordBtn.disabled = !hasNextSequence || isRecording;
  };

  const doRecord = async () => {
    if (isRecording) return;
    const toStep = currentStep + 1;
    if (toStep > maxStep) return;
    await onRecord(currentStep, toStep);
    currentStep = toStep;
    updateDisplay();
  };

  prevBtn.addEventListener('click', () => {
    if (currentStep > 0 && !isRecording) {
      const fromStep = currentStep;
      currentStep--;
      onStepChange(fromStep, currentStep);
      updateDisplay();
    }
  });

  nextBtn.addEventListener('click', () => {
    if (currentStep < maxStep && !isRecording) {
      const fromStep = currentStep;
      currentStep++;
      onStepChange(fromStep, currentStep);
      updateDisplay();
    }
  });

  recordBtn.addEventListener('click', doRecord);

  document.addEventListener('keydown', (e) => {
    if (e.key.toLowerCase() === 'r' && !isRecording) {
      doRecord();
    }
    if (e.key === 'ArrowLeft' && currentStep > 0 && !isRecording) {
      const fromStep = currentStep;
      currentStep--;
      onStepChange(fromStep, currentStep);
      updateDisplay();
    }
    if (e.key === 'ArrowRight' && currentStep < maxStep && !isRecording) {
      const fromStep = currentStep;
      currentStep++;
      onStepChange(fromStep, currentStep);
      updateDisplay();
    }
  });

  const originalUpdateStatus = updateStatus;
  (window as unknown as { __updateRecStatus: (msg: string) => void }).__updateRecStatus = (
    msg: string
  ) => {
    statusEl.textContent = msg;
    originalUpdateStatus(msg);
  };

  return {
    updateStep(step: number, max: number) {
      currentStep = step;
      maxStep = max;
      updateDisplay();
    },
  };
}

export function setupSequenceRecording(config: SequenceRecordingConfig): void {
  const { canvasSelector, sequenceMap, recordingConfig = {} } = config;
  const fullConfig = { ...DEFAULT_CONFIG, ...recordingConfig };

  const updateStatus = (msg: string) => {
    const el = document.getElementById('rec-status');
    if (el) el.textContent = msg;
  };

  const checkReady = setInterval(() => {
    const canvas = document.querySelector(canvasSelector) as HTMLCanvasElement;
    if (!canvas?.__state__) return;

    clearInterval(checkReady);
    const state = canvas.__state__ as State;

    const controllers = [...state.query([StepController])];
    const controllerEid = controllers[0];
    if (controllerEid === undefined) {
      updateStatus('No StepController found');
      return;
    }

    const doRecord = async (fromStep: number, toStep: number) => {
      const seqName = sequenceMap[`${fromStep}-${toStep}`];
      if (!seqName) {
        updateStatus(`No sequence for ${fromStep}-${toStep}`);
        return;
      }

      const seqEid = getEntityByName(seqName);
      if (seqEid === null) {
        updateStatus(`Sequence ${seqName} not found`);
        return;
      }

      isRecording = true;

      const origWidth = canvas.width;
      const origHeight = canvas.height;
      const origStyle = canvas.style.cssText;

      document.body.classList.add('recording-mode');
      canvas.classList.add('recording-target');
      canvas.width = fullConfig.width;
      canvas.height = fullConfig.height;
      canvas.style.cssText = `width:${fullConfig.width}px;height:${fullConfig.height}px;display:block`;
      window.dispatchEvent(new Event('resize'));

      await new Promise((r) => requestAnimationFrame(r));

      updateStatus('Recording...');
      const startTime = performance.now();

      const blob = await encodeSequenceFrames(canvas, state, seqEid, fullConfig, (frame, total) => {
        updateStatus(`Recording: ${frame}/${total}`);
      });

      const elapsed = performance.now() - startTime;
      let maxDuration = 0;
      for (const childEid of state.query([Pair(ChildOf.relation, seqEid), Tween])) {
        maxDuration = Math.max(maxDuration, Tween.duration[childEid]);
      }
      const duration = maxDuration + 0.1;
      const speed = (duration * 1000) / elapsed;

      document.body.classList.remove('recording-mode');
      canvas.classList.remove('recording-target');
      canvas.width = origWidth;
      canvas.height = origHeight;
      canvas.style.cssText = origStyle;
      window.dispatchEvent(new Event('resize'));

      // Update step controller to reflect new state
      StepController.target[controllerEid] = toStep;

      const filename = `step-${fromStep}-${toStep}.mp4`;
      downloadBlob(blob, filename);

      updateStatus(`Done! ${(blob.size / 1024 / 1024).toFixed(1)}MB, ${speed.toFixed(1)}x`);
      isRecording = false;
    };

    const onStepChange = (_fromStep: number, toStep: number) => {
      StepController.target[controllerEid] = toStep;
    };

    const uiController = createUI(fullConfig, sequenceMap, doRecord, onStepChange, updateStatus);
    const maxStep = getMaxStepFromMap(sequenceMap);
    uiController.updateStep(StepController.step[controllerEid], maxStep);
    updateStatus('Ready');
  }, 100);
}
